section {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
}
section .color {
  position: absolute;
  filter: blur(150px);
}
section .color:nth-child(1) {
  top: -350px;
  width: 600px;
  height: 600px;
  background: #ff359b;
}
section .color:nth-child(2) {
  bottom: -150px;
  left: 100px;
  width: 500px;
  height: 500px;
  background: #fffd87;
}
section .color:nth-child(3) {
  bottom: 50px;
  right: 100px;
  width: 300px;
  height: 300px;
  background: #00ffbf;
}
section .color:nth-child(4) {
  bottom: 150px;
  right: 600px;
  width: 300px;
  height: 300px;
  background: #00ffff;
}

.box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.box .circle {
  position: absolute;
  backdrop-filter: blur(5px);
  box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  backdrop-filter: hue-rotate(20deg);
  animation: animate 10s linear infinite;
  animation-delay: calc(-2s * var(--i));
}
@keyframes animate {
  0%,
  100% {
    transform: translateY(-40px);
  }
  50% {
    transform: translateY(40px);
  }
}
.box .circle:nth-child(1) {
  top: -150px;
  right: -160px;
  width: 250px;
  height: 250px;
}
.box .circle:nth-child(2) {
  top: 150px;
  left: -140px;
  width: 200px;
  height: 200px;
}
.box .circle:nth-child(3) {
  bottom: 50px;
  right: -60px;
  width: 80px;
  height: 80px;
  z-index: 2;
}
.box .circle:nth-child(4) {
  bottom: -80px;
  left: 100px;
  width: 50px;
  height: 50px;
}
.box .circle:nth-child(5) {
  top: -80px;
  left: 140px;
  width: 60px;
  height: 60px;
  z-index: 2;
}

.todoForm {
  position: relative;
  min-width: 700px;
  height: 80vh;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  backdrop-filter: blur(5px);
  box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

input {
  padding: 10px 20px;
  font-size: 15px;
  border-radius: 3px;
  background: transparent;
  outline: none;
  border: none;
}
input:focus {
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}
input::placeholder {
  color: rgba(0, 0, 0, 0.5);
}

.form {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 20px 40px 10px 40px;
  margin-bottom: 40px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
}
.form form,
.search {
  height: 50px;
  display: flex;
  padding: 0 20px;
  border-right: 1px solid rgba(255, 255, 255, 0.5);
}
.form form:last-child {
  border: none;
}

.add {
  flex: 1;
}
.add > input {
  width: 250px;
}
.add > button {
  display: flex !important;
  align-items: center !important;
  margin: 0 10px !important;
  padding: 10px !important;
  font-size: 16px;
  text-transform: capitalize !important;
}
.search > input {
  width: 150px;
}
.completed {
  display: flex;
  align-items: center;
}

.todos {
  font-size: 20px;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  padding: 10px;
  overflow: scroll;
}
.todos::-webkit-scrollbar {
  width: 3px;
}
.todos::-webkit-scrollbar-corner {
  display: none;
}
.todos::-webkit-scrollbar-thumb {
  background-color: grey;
  border-radius: 5px;
}
.todos > span,
.todos > strike {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 3px;
}
.todos > span > h5,
.todos > strike > h5 {
  display: flex;
  font-weight: 400;
  box-sizing: border-box;
  padding-left: 10px;
  width: 85%;
  height: 100%;
  align-items: center;
}
.todos > span:hover,
.todos > strike:hover {
  backdrop-filter: blur(5px);
  box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 5px;
}
.todos > span:hover .tools,
.todos > strike:hover .tools {
  opacity: 1 !important;
  pointer-events: all;
}

.tools {
  opacity: 0;
  pointer-events: none;
}
